/* global chrome */
import React, { Component } from 'react';
import { Collapse, Avatar, Tabs } from 'antd';
import { DndProvider, DragSource, DropTarget } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import get from 'lodash/get';
import ReactGA from 'react-ga';
import facebookAvatar from './icons/facebook.png';
import youtubeAvatar from './icons/youtube.png';
import twitterAvatar from './icons/twitter.png';
import redditAvatar from './icons/reddit.png';
import netflixAvatar from './icons/netflix.png';
import linkedinAvatar from './icons/linkedin.png';
import configAvatar from './icons/config.png';
import './App.css';

import FilterList from './FilterList';
const { TabPane } = Tabs;

const panelNode = (avatarSrc, header) => (
  <div className="panel-node">
    <Avatar className="panel-logo" src={avatarSrc} />
    <span className="panel-header-title">{header}</span>
  </div>
);

class App extends Component {
  state = {
    twitterSettings: {},
    youtubeSettings: {},
    facebookSettings: {},
    redditSettings: {},
    netflixSettings: {},
    linkedinSettings: {},
    generalSettings: {},
    tabOrder: [1, 2, 3, 4, 5, 6, 7],
  };

  updateFilterValue = (filterCategory, filterKey, filterValue) => {
    const tempSettings = this.state[filterCategory];
    tempSettings[filterKey].value = filterValue;
    this.setState({ [filterCategory]: tempSettings }, () => {
      if (chrome.storage) {
        chrome.storage.sync.set({
          [filterCategory]: this.state[filterCategory],
        });
      }
    });
  };

  componentDidMount = () => {
    ReactGA.initialize('UA-131792384-1');
    ReactGA.set({ checkProtocolTask: null });

    ReactGA.pageview('Home Page');

    // fetch settings from storage and set UI state
    if (chrome.storage) {
      chrome.storage.sync.get(
        [
          'twitterSettings',
          'youtubeSettings',
          'facebookSettings',
          'redditSettings',
          'netflixSettings',
          'linkedinSettings',
          'generalSettings',
        ],
        ({
          twitterSettings = {},
          youtubeSettings = {},
          facebookSettings = {},
          redditSettings = {},
          netflixSettings = {},
          linkedinSettings = {},
          generalSettings = {},
        }) => {
          this.setState((prevState, props) => {
            return {
              twitterSettings,
              youtubeSettings,
              facebookSettings,
              redditSettings,
              netflixSettings,
              linkedinSettings,
              generalSettings,
            };
          });
        }
      );

      // Update local generalSettings value on timer completion
      chrome.storage.onChanged.addListener((changes, namespace) => {
        const [filterCategory, bothChanges] = Object.entries(changes)[0];
        const newSettings = bothChanges.newValue;
        const oldSettings = bothChanges.oldValue;
        if (
          filterCategory === 'generalSettings' &&
          newSettings.disableFiltersTemporary.value.active !==
            oldSettings.disableFiltersTemporary.value.active
        ) {
          this.setState({ generalSettings: newSettings });
        }
      });
    }

    var tabOrder = JSON.parse(localStorage.getItem('tabOrder'));
    tabOrder =
      Array.isArray(tabOrder) && tabOrder.length == 7
        ? tabOrder
        : [1, 2, 3, 4, 5, 6, 7];
    this.setState({ tabOrder });
  };

  render() {
    const Panel = Collapse.Panel;
    Panel.defaultProps = {
      showArrow: true,
    };
    const currentTime =
      new Date()
        .getHours()
        .toString()
        .padStart(2, '0') +
      ':' +
      new Date()
        .getMinutes()
        .toString()
        .padStart(2, '0');
    const fromTime = get(
      this.state.generalSettings,
      'disableDuringHours.value.fromTime',
      false
    );
    const toTime = get(
      this.state.generalSettings,
      'disableDuringHours.value.toTime',
      false
    );

    const disableFilters =
      get(this.state.generalSettings, 'disableFilters.value', false) ||
      get(
        this.state.generalSettings,
        'disableFiltersTemporary.value.active',
        false
      ) ||
      (get(
        this.state.generalSettings,
        'disableDuringHours.value.active',
        false
      ) &&
        (fromTime < toTime
          ? fromTime <= currentTime && currentTime < toTime
          : (fromTime <= currentTime && currentTime <= '23:59') ||
            ('00:00' <= currentTime && currentTime < toTime)));

    // Drag & Drop node
    class TabNode extends React.Component {
      render() {
        const { connectDragSource, connectDropTarget, children } = this.props;

        return connectDragSource(connectDropTarget(children));
      }
    }

    const cardTarget = {
      drop(props, monitor) {
        const dragKey = monitor.getItem().index;
        const hoverKey = props.index;

        if (dragKey === hoverKey) {
          return;
        }

        props.moveTabNode(dragKey, hoverKey);
        monitor.getItem().index = hoverKey;
      },
    };

    const cardSource = {
      beginDrag(props) {
        return {
          id: props.id,
          index: props.index,
        };
      },
    };

    const WrapTabNode = DropTarget('DND_NODE', cardTarget, (connect) => ({
      connectDropTarget: connect.dropTarget(),
    }))(
      DragSource('DND_NODE', cardSource, (connect, monitor) => ({
        connectDragSource: connect.dragSource(),
        isDragging: monitor.isDragging(),
      }))(TabNode)
    );

    class DraggableTabs extends React.Component {
      state = {
        order: [],
      };

      moveTabNode = (dragKey, hoverKey) => {
        const newOrder = this.state.order.slice();
        const { children } = this.props;

        React.Children.forEach(children, (c) => {
          if (newOrder.indexOf(c.key) === -1) {
            newOrder.push(c.key);
          }
        });

        const dragIndex = newOrder.indexOf(dragKey);
        const hoverIndex = newOrder.indexOf(hoverKey);

        newOrder.splice(dragIndex, 1);
        newOrder.splice(hoverIndex, 0, dragKey);

        this.setState(
          {
            order: newOrder,
          },
          () => {
            localStorage.setItem('tabOrder', JSON.stringify(this.state.order));
            this.props.updateParentTabOrder(this.state.order);
          }
        );
      };

      renderTabBar = (props, DefaultTabBar) => (
        <DefaultTabBar {...props}>
          {(node) => (
            <WrapTabNode
              key={node.key}
              index={node.key}
              moveTabNode={this.moveTabNode}
            >
              {node}
            </WrapTabNode>
          )}
        </DefaultTabBar>
      );

      render() {
        const { order } = this.state;
        const { children } = this.props;

        const tabs = [];
        React.Children.forEach(children, (c) => {
          tabs.push(c);
        });

        const orderTabs = tabs.slice().sort((a, b) => {
          const orderA = order.indexOf(a.key);
          const orderB = order.indexOf(b.key);

          if (orderA !== -1 && orderB !== -1) {
            return orderA - orderB;
          }
          if (orderA !== -1) {
            return -1;
          }
          if (orderB !== -1) {
            return 1;
          }

          const ia = tabs.indexOf(a);
          const ib = tabs.indexOf(b);

          return ia - ib;
        });

        return (
          <DndProvider backend={HTML5Backend}>
            <Tabs renderTabBar={this.renderTabBar} {...this.props}>
              {orderTabs}
            </Tabs>
          </DndProvider>
        );
      }
    }

    const tabPanes = [
      <TabPane
        className={`header header-facebook`}
        tab={panelNode(facebookAvatar, 'Facebook')}
        key="1"
      >
        <FilterList
          filterCategory={'facebookSettings'}
          settings={this.state.facebookSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-youtube`}
        tab={panelNode(youtubeAvatar, 'Youtube')}
        key="2"
      >
        <FilterList
          filterCategory={'youtubeSettings'}
          settings={this.state.youtubeSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-twitter`}
        tab={panelNode(twitterAvatar, 'Twitter')}
        key="3"
      >
        <FilterList
          filterCategory={'twitterSettings'}
          settings={this.state.twitterSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-reddit`}
        tab={panelNode(redditAvatar, 'Reddit')}
        key="4"
      >
        <FilterList
          filterCategory={'redditSettings'}
          settings={this.state.redditSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-netflix`}
        tab={panelNode(netflixAvatar, 'Netflix')}
        key="5"
      >
        <FilterList
          filterCategory={'netflixSettings'}
          settings={this.state.netflixSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-linkedin`}
        tab={panelNode(linkedinAvatar, 'LinkedIn')}
        key="6"
      >
        <FilterList
          filterCategory={'linkedinSettings'}
          settings={this.state.linkedinSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
      <TabPane
        className={`header header-general`}
        tab={panelNode(configAvatar, 'Settings')}
        key="7"
      >
        <FilterList
          filterCategory={'generalSettings'}
          settings={this.state.generalSettings}
          hideTooltip={get(
            this.state.generalSettings,
            'hideTooltip.value',
            false
          )}
          updateFilterValue={this.updateFilterValue}
        />
      </TabPane>,
    ];

    const sortedTabPanes = (tabOrder) =>
      tabOrder.map((order) => tabPanes[order - 1]);

    return (
      <DraggableTabs
        className={`undistracted-tabs ${
          disableFilters ? 'disabled-filters' : ''
        }`}
        tabPosition="left"
        size="small"
        defaultActiveKey={localStorage.getItem('activeKey') || 1}
        onChange={(activeKey) => {
          localStorage.setItem('activeKey', activeKey);
        }}
        updateParentTabOrder={(tabOrder) => this.setState({ tabOrder })}
      >
        {sortedTabPanes(this.state.tabOrder)}
      </DraggableTabs>
    );
  }
}

export default App;
