import React, { Component } from 'react';
import { Form, Input, Button } from 'antd';
import debounce from 'lodash/debounce';

const { Item: FormItem } = Form;

function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class TextFilter extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      this.props.submittable && this.props.handleSubmit(values.customURL);
    });
  };

  handleChange = debounce(async e => {
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      !this.props.submittable && this.props.updateCustomURL(values.customURL);
    });
  }, 500);

  render() {
    const {
      form: { getFieldDecorator, getFieldsError },
      currentURL,
      submittable
    } = this.props;
    return (
      <Form
        layout="inline"
        className={`url-form ${this.props.submittable ? 'submittable' : ''}`}
        onSubmit={submittable && this.handleSubmit}
      >
        <FormItem hasFeedback className="url-form-item">
          {getFieldDecorator('customURL', {
            rules: [
              {
                message: 'Invalid URL',
                pattern: /[(http(s)?):\/\/(www\.)?a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/gi
              }
            ]
          })(
            <Input
              className="url-form-input"
              addonBefore="https://"
              onChange={this.handleChange}
              size="small"
              placeholder={currentURL || this.props.placeholderURL}
              autoComplete="off"
            />
          )}
        </FormItem>
        {submittable && (
          <FormItem className="url-form-item">
            <Button
              type="primary"
              htmlType="submit"
              size="small"
              className="url-form-submit"
              disabled={hasErrors(getFieldsError())}
            >
              +
            </Button>
          </FormItem>
        )}
      </Form>
    );
  }
}

const WrappedTextFilter = Form.create()(TextFilter);

export default WrappedTextFilter;
