/* global chrome */
import React from 'react';
import moment from 'moment';
import { List, Switch, Radio, Tooltip, Icon, Card, Tag } from 'antd';
import TimePicker from 'rc-time-picker';
import 'rc-time-picker/assets/index.css';
import './pause.png';

import WrappedTextFilter from './TextFilter';
import TextArrayFilter from './TextArrayFilter';

const pauseTime = 5 * 60 * 1000;

function onFilterValueChange(
  filterCategory,
  filterKey,
  checked,
  updateFilterValue
) {
  var filterValue = checked;
  if (filterCategory === 'generalSettings' && filterKey === 'disableFilters') {
    if (checked) {
      chrome.browserAction.setBadgeBackgroundColor({ color: '#FF0000' });
      chrome.browserAction.setTitle({
        title: 'Paused. Resume under *General Settings > Pause all filters*',
      });
      chrome.browserAction.setBadgeText({ text: '!' });
    } else {
      chrome.browserAction.setTitle({
        title: 'UnDistracted',
      });
      chrome.browserAction.setBadgeText({ text: '' });
    }
  } else if (
    filterCategory === 'generalSettings' &&
    filterKey === 'disableFiltersTemporary'
  ) {
    filterValue = {
      active: filterValue,
      endTimestamp: filterValue ? Date.now() + pauseTime : '', // add 5 minutes for timer
    };
  } else if (
    filterCategory === 'generalSettings' &&
    filterKey === 'disableDuringHours'
  ) {
    filterValue = {
      active: filterValue.active,
      fromTime: filterValue.fromTime,
      toTime: filterValue.toTime,
    };
  }
  updateFilterValue(filterCategory, filterKey, filterValue);
}

const renderFilterItem = (
  filterCategory,
  filterKey,
  filterData,
  hideTooltip,
  updateFilterValue
) => {
  return (
    <Tooltip
      placement="top"
      arrowPointAtCenter
      title={hideTooltip ? '' : filterData.tooltip}
      mouseEnterDelay={1}
    >
      {(() => {
        switch (filterData.type) {
          case 'switch':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription">
                  {filterData.description}
                </div>
                <Switch
                  checked={filterData.value}
                  onChange={(checked) =>
                    onFilterValueChange(
                      filterCategory,
                      filterKey,
                      checked,
                      updateFilterValue
                    )
                  }
                />
              </List.Item>
            );
          case 'switch-multi':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription">
                  {filterData.description}
                </div>
                <Radio.Group
                  buttonStyle="solid"
                  size="small"
                  defaultValue={filterData.value}
                  value={filterData.value}
                  onChange={(e) => {
                    onFilterValueChange(
                      filterCategory,
                      filterKey,
                      e.target.value,
                      updateFilterValue
                    );
                  }}
                >
                  <Radio.Button value="0">Show</Radio.Button>
                  <Radio.Button value="1">Blur</Radio.Button>
                  <Radio.Button value="2">Hide</Radio.Button>
                </Radio.Group>
              </List.Item>
            );
          case 'switch-with-meta':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription">
                  {filterData.description}
                </div>
                <Switch
                  checked={filterData.value.active}
                  onChange={(checked) =>
                    onFilterValueChange(
                      filterCategory,
                      filterKey,
                      checked,
                      updateFilterValue
                    )
                  }
                />
              </List.Item>
            );
          case 'switch-with-time-period':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription complex-filter">
                  {filterData.description}
                </div>
                <div className="time-list-controls">
                  <div className="timePickers">
                    <TimePicker
                      showSecond={false}
                      className="timePicker"
                      minuteStep={15}
                      inputReadOnly
                      use12Hours
                      allowEmpty={false}
                      placeholder="From"
                      value={
                        filterData.value.fromTime
                          ? moment(filterData.value.fromTime, 'HH:mm')
                          : null
                      }
                      onChange={(time) =>
                        onFilterValueChange(
                          filterCategory,
                          filterKey,
                          {
                            active: filterData.value.active,
                            fromTime: time.format('HH:mm').toString(),
                            toTime: filterData.value.toTime,
                          },
                          updateFilterValue
                        )
                      }
                      format={'h:mm a'}
                    />
                    <Tag>to</Tag>
                    <TimePicker
                      showSecond={false}
                      className="timePicker"
                      minuteStep={15}
                      inputReadOnly
                      use12Hours
                      allowEmpty={false}
                      placeholder="Until"
                      value={
                        filterData.value.toTime
                          ? moment(filterData.value.toTime, 'HH:mm')
                          : null
                      }
                      onChange={(time) =>
                        onFilterValueChange(
                          filterCategory,
                          filterKey,
                          {
                            active: filterData.value.active,
                            fromTime: filterData.value.fromTime,
                            toTime: time.format('HH:mm').toString(),
                          },
                          updateFilterValue
                        )
                      }
                      format={'h:mm a'}
                    />
                  </div>
                  <Switch
                    checked={filterData.value.active}
                    onChange={(checked) =>
                      onFilterValueChange(
                        filterCategory,
                        filterKey,
                        {
                          active: checked,
                          fromTime: filterData.value.fromTime,
                          toTime: filterData.value.toTime,
                        },
                        updateFilterValue
                      )
                    }
                  />
                </div>
              </List.Item>
            );
          case 'text':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription complex-filter">
                  {filterData.description}
                </div>
                <WrappedTextFilter
                  currentURL={filterData.value}
                  updateCustomURL={(customURL) => {
                    onFilterValueChange(
                      filterCategory,
                      filterKey,
                      customURL,
                      updateFilterValue
                    );
                  }}
                />
              </List.Item>
            );
          case 'text-list':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div className="filterDescription complex-filter">
                  {filterData.description}
                </div>
                <div className="text-list-controls">
                  <TextArrayFilter
                    currentURLS={filterData.value.customURLList}
                    updateCustomURLS={(customURLList) => {
                      onFilterValueChange(
                        filterCategory,
                        filterKey,
                        { active: filterData.value.active, customURLList },
                        updateFilterValue
                      );
                    }}
                  />
                  <Switch
                    checkedChildren={filterData.value.customURLList.length}
                    unCheckedChildren={filterData.value.customURLList.length}
                    checked={filterData.value.active}
                    onChange={(checked) =>
                      onFilterValueChange(
                        filterCategory,
                        filterKey,
                        {
                          active: checked,
                          customURLList: filterData.value.customURLList,
                        },
                        updateFilterValue
                      )
                    }
                  />
                </div>
              </List.Item>
            );
          case 'button-list':
            return (
              <List.Item
                className={`${filterData.customClass || ''}`}
                style={{ padding: '0' }}
              >
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                  }}
                >
                  {filterData.buttonList.map((buttonItem, index) => (
                    <Card
                      bordered={false}
                      style={{
                        cursor: 'pointer',
                        flexGrow: `${index === 1 ? '1' : ''}`,
                        boxShadow:
                          '1px 0 0 0 #e8e8e8, 0 1px 0 0 #e8e8e8, 1px 1px 0 0 #e8e8e8, inset 1px 0 0 0 #e8e8e8, inset 0 1px 0 0 #e8e8e8',
                      }}
                      bodyStyle={{
                        padding: '8px',
                      }}
                      key={buttonItem.title}
                      onClick={() => {
                        switch (buttonItem.action) {
                          case 'mail':
                            chrome.tabs.create({
                              url: `mailto:${buttonItem.to}`,
                            });
                            break;
                          case 'redirect':
                            chrome.tabs.create({
                              url: buttonItem.to,
                            });
                            break;
                          default:
                            break;
                        }
                      }}
                    >
                      <div
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                        }}
                      >
                        <Icon
                          type={buttonItem.icon}
                          fontSize={'larger'}
                          theme="filled"
                          style={{ color: `${buttonItem.iconColor}` }}
                        />
                        <div
                          style={{
                            whiteSpace: 'nowrap',
                            marginLeft: '3px',
                            fontSize: 'smaller',
                          }}
                        >
                          {buttonItem.title}
                        </div>
                      </div>
                    </Card>
                  ))}
                </div>
              </List.Item>
            );
          case 'image':
            return (
              <List.Item
                className={`filterListItem ${filterData.customClass || ''}`}
              >
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                    'justify-content': 'center',
                  }}
                  dangerouslySetInnerHTML={{ __html: filterData.link }}
                />
              </List.Item>
            );
          default:
            return <div />;
        }
      })()}
    </Tooltip>
  );
};

const FilterList = (props) => {
  return (
    <List
      bordered
      style={{ border: '0' }}
      dataSource={Object.entries(props.settings)
        .sort(function(a, b) {
          return a[1].order - b[1].order;
        })
        .filter((item) =>
          item[1].hasOwnProperty('enabled') && !item[1].enabled ? false : true
        )}
      renderItem={(item) =>
        renderFilterItem(
          props.filterCategory,
          item[0],
          item[1],
          props.hideTooltip,
          props.updateFilterValue
        )
      }
    />
  );
};

export default FilterList;
