import React, { Component } from 'react';
import { Button, Modal, Tag, Tooltip, message } from 'antd';
import WrappedTextFilter from './TextFilter';

const slicedUrl = url => `${url.slice(0, 20)}...`;

class TextArrayFilter extends Component {
  state = { visible: false };

  handleOk = e => {
    this.setState({ visible: false });
  };

  handleSubmit = customURL => {
    this.props.updateCustomURLS([...this.props.currentURLS, customURL]);
    message.success(
      `${customURL.length > 20 ? slicedUrl(customURL) : customURL} added`
    );
  };

  handleTagDelete = (index, e, url) => {
    e.preventDefault();
    const currentURLSTemp = this.props.currentURLS.slice();
    currentURLSTemp.splice(index, 1);
    this.props.updateCustomURLS(currentURLSTemp);
    message.success(`${url.length > 20 ? slicedUrl(url) : url} removed`);
  };

  render() {
    return (
      <div>
        <Button
          onClick={() => this.setState({ visible: true })}
          icon="edit"
          size="small"
        >
          Domains to block
        </Button>
        <Modal
          footer={[
            <Button
              key="Ok"
              type="primary"
              onClick={() => this.setState({ visible: false })}
            >
              Ok
            </Button>
          ]}
          closable={false}
          visible={this.state.visible}
          onCancel={() => this.setState({ visible: false })}
        >
          <WrappedTextFilter
            handleSubmit={this.handleSubmit}
            placeholderURL="www.instagram.com"
            submittable
          />
          <div className="url-tags">
            {this.props.currentURLS.map((url, index) => (
              <Tag closable onClose={e => this.handleTagDelete(index, e, url)}>
                {url.length > 20 ? (
                  <Tooltip title={url}>{slicedUrl(url)}</Tooltip>
                ) : (
                  url
                )}
              </Tag>
            ))}
          </div>
        </Modal>
      </div>
    );
  }
}

export default TextArrayFilter;
